.backButton {
  display: flex;
  align-items: center;
  padding: 0.8rem;
  margin: 2rem 6rem;
  border-radius: 5px;
  gap: 0.5rem;
  background: #fff;
  color: #000;
  border: none;
  cursor: pointer;
}

.backButton:hover {
  transition: 0.3s;
  background-color: #f37218;
  color: #fff;
}

.editButton {
  background: #14b8;
}

.dashboardCard {
  max-width: 1400px;
  margin: 2rem auto;
  background: linear-gradient(135deg, #ffffff 0%, #f8fafc 100%);
  border-radius: 1rem;
  width: 100%;
  overflow: hidden;
}

.dashboardHeader {
  padding: 1.5rem;
  background: linear-gradient(to right, #14b8a1, #14b8);
  color: white;
}

.dashboardTitle {
  font-size: 1.5rem;
  font-weight: 600;
  line-height: 1.25;
}

.dashboardContent {
  padding: 1.5rem;
}

.controls {
  display: flex;
  gap: 1rem;
  margin-bottom: 1.5rem;
  flex-wrap: wrap;
}

.searchWrapper {
  position: relative;
  flex: 1;
  min-width: 280px;
}

.deleteButton {
  background-color: #f87171;
  color: white;
}

.deleteButton:hover {
  background-color: #ef4444;
}

.cancelButton {
  background: #f87171;
}

.searchIcon {
  position: absolute;
  left: 0.75rem;
  top: 50%;
  transform: translateY(-50%);
  color: #64748b;
  pointer-events: none;
}

.searchInput {
  width: 100%;
  padding-left: 2.5rem;
}

.filtersWrapper {
  display: flex;
  gap: 0.75rem;
  flex-wrap: wrap;
}

.filterSelect {
  min-width: 160px;
}

.tableWrapper {
  border: 1px solid #e2e8f0;
  border-radius: 0.5rem;
  overflow: hidden;
}

.sortableHeader {
  font-weight: 600;
  cursor: pointer;
  text-align: center;
  user-select: none;
}

.sortableHeader:hover {
  background-color: #f8fafc;
}

.sortIcon {
  display: inline-flex;
  align-items: center;
  margin-left: 0.5rem;
}

.tableRow {
  transition: background-color 0.2s ease;
}

.tableRow:hover {
  background-color: #f8fafc;
}

.badge {
  text-transform: capitalize;
  font-size: 0.75rem;
  font-weight: 500;
}

.badge.active {
  background-color: #dcfce7;
  color: #166534;
}

.badge.inactive {
  background-color: #fee2e2;
  color: #991b1b;
}

.badge.admin {
  background-color: #e0e7ff;
  color: #3730a3;
}

.badge.user {
  background-color: #f3f4f6;
  color: #374151;
}

.actionButtons {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
}

.buttonIcon {
  margin-right: 0.25rem;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
}

.formGroup {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.formActions {
  display: flex;
  justify-content: flex-end;
  gap: 0.75rem;
  margin-top: 1rem;
}

.dialogContent {
  max-width: 500px;
}

.dialogActions {
  display: flex;
  justify-content: flex-end;
  gap: 0.75rem;
  margin-top: 1.5rem;
}

@media (max-width: 768px) {
  .backButton {
    margin: 1rem;
  }

  .controls {
    flex-direction: column;
  }

  .searchWrapper {
    width: 100%;
  }

  .filtersWrapper {
    width: 100%;
  }

  .filterSelect {
    flex: 1;
  }

  .actionButtons {
    flex-direction: column;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(4px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.tableRow {
  animation: fadeIn 0.2s ease-out forwards;
}
